import { ArrowDownOutlined, DeleteOutlined } from '@ant-design/icons';
import { App, Avatar, Button, Divider, Form, Input, List, Row, Col,Card, Statistic, Segmented, Typography, Space, Image, Tag, Progress, Popconfirm, Select } from "antd"
import useAPIKey from "../../hooks/useAPIKey"
import { red, green } from '@ant-design/colors';
import useLogin from '../../comp/login';
import { userStore } from '../../store/userStore';
import { observer } from 'mobx-react-lite';
import { productStore } from '../../store/productStore';
import './index.css';
import dayjs from 'dayjs';
import EditableC from '../../comp/editable';
import usePaymentPanel from '../../comp/payment';
import { useNavigate } from 'react-router-dom';

const { Title } = Typography;

const udata = [
    {username: 'usdfsfsdf1'},{},{username: 'u3'},
    // {username: 'u1'},{username: 'u2'},{username: 'u3'},
    // {username: 'u1'},{username: 'u2'},{username: 'u3'},
    // {username: 'u1'},{username: 'u2'},{username: 'u3'}

];


 function About() {
    //
    const nav = useNavigate();

    // pyament
    const paymentPanel = usePaymentPanel({title: '账单'});
 
    const {message} = App.useApp();
    const login = useLogin();
    const [value, setValue] = useAPIKey();
 
    // login panael
    const loginTo = (values, callbk) => {

        userStore.login(values, accessToken =>{ setValue(accessToken); callbk();}, message)
    }


    // tips

    const goToDetail = (item) => {
        console.info(item.payments)
        productStore.selectd(item);
        nav('/product/detail');
    }

    // action buton
    const actionList = (item) => {

        let list = [];
        const edit = <a onClick={()=>goToDetail(item)}>编辑</a>;
        const del = <Popconfirm
                        placement="topRight"
                        title="产品移除"
                        description={`${item.style}-${item.model}`}
                        onConfirm={()=>productStore.del(item.id)}
                        okText="Yes"
                        cancelText="No"
                    >
                        <DeleteOutlined  style={{color: 'red'}}/> 移除
                    </Popconfirm>

        const sampleCost = <Tag color='success'>{item.sampleOrder.studioName} {item.sampleOrder.producerName ? item.sampleOrder.producerName + ' ' + item.sampleOrder.cost + ' 小时' : "排队中"}</Tag>



        switch (item.productStatus) {
            case 'pending':
                list.push(del);
                break;
            case 'studio':
                list.push(sampleCost);
                break;
        
            case 'factory':
                let factoryCost = 0;
                for (const meterial of item.materials) {
                    factoryCost+=meterial.amount
                }
                const cost = <Tag color='error'>{factoryCost} 元</Tag>
                list.push(cost);
                break
            default:
                break;
        }

        list.push(edit)
        return list;
        
    }

    const getkanB = () =>{
        
        return productStore.getFilterTemplate.map(item => 
            (
                {
                    label: <Statistic
                                title={item.title}
                                value={item.value}
                                valueStyle={{
                                    color: item.color,
                                }}
                                />,
                    value: item.key
                }
            )
        )
    }

    return (
  
        <div className='about'>

        <Select 
            defaultValue="jack"

            options={[
                {value: 'jack', label: "Jack"},
                {value: 'solar', label: "Solar"},
                {value: 'boro', label: "Boro"},
            ]}
        />

        {userStore.user.fullname}   

    
        <Button onClick={()=>login(loginTo)}>called</Button>
        <Button onClick={()=> productStore.index() }>products</Button>
        <Button onClick={()=> paymentPanel((values,callbk)=> console.log(values)) }>payment</Button>


        <Divider orientation='left'>概要</Divider>
            
        <Segmented
            default={productStore.filter}
            onChange={productStore.updateFilter}
            block
            options={getkanB()}
        />

        <Card
            
        >
        <List 
            size='middle'
            itemLayout="horizontal"
            pagination={{
                
                pageSize:5,
                simple: true,
                position: 'bottom',
                align: 'end'
        
            }}
            dataSource={productStore.getByFilter()}
            renderItem={
                value => 
                <List.Item
                    actions={actionList(value)}
                >
           
                    <List.Item.Meta 
                        avatar={<Image
                                    width={30}
                                    height={40}
                                    src="error"
                                    fallback="data:image/png;base64,"
                                />}
                        title={<>{value.style} </>}
                        description={value.model}
                    />

                
                </List.Item>
            }
        />      
        
        </Card>
        <br />
        <Button onClick={() => productStore.new()} size='large' type='primary' block> 客单添加 </Button>
        </div>
        
    )
}



export default observer( About)